import Mock from 'mockjs';

let posunsualData = Mock.mock({
    "warningTypes|1-1": [
        {value: "0", label: ""},
        {value: "1", label: ""},
        {value: "2", label: ""},
        {value: "3", label: ""}
    ],
    "prisonerTypes|1-1": [
        {value: "0", label: ""},
        {value: "1", label: ""},
        {value: "2", label: ""},
        {value: "3", label: ""},
        {value: "4", label: ""},
        {value: "5", label: ""}
    ],
    ppuTableDatas :  {
        "totalRows": Mock.mock('@natural(1, 100)'),
        items: []
    },
    prisonerInfo : {
        prisonerName: "",
        prisonerNum: "00500",
        room: "3232",
        prisonerType: "",
        crime: "",
        sex: "",
        age: "35",
        group: "121",
        crimeTime: "2014/02/15",
        count: "3"
    }
})

for (let i = 0; i < 10; i++) {
    posunsualData.ppuTableDatas.items.push({
        startTime: "2017-07-07 12:22:12",
        endTime: "2017-07-07 12:22:12",
        timeLen: "21min",
        prisonerName: "",
        prisonerNum: "00500",
        prisonerType: "",
        warningType: "",
        warningArea: "2321",
        warningRule: "",
        warningTv: "a.mp4"
    });
}

export default posunsualData;


