let cameraData = {
    "totalRows": Mock.mock('@natural(1, 100)'),
    items: []
}

for (let i = 0; i < 10; i++) {
    cameraData.items.push({
        startTime: "2017-07-07 12:22:12",
        endTime: "2017-07-07 12:22:12",
        timeLen: "21min",
        prisonerName: "",
        prisonerNum: "00500",
        prisonerType: "",
        warningType: "",
        warningArea: "2321",
        warningRule: "",
        warningTv: "a.mp4"
    });
}

export default cameraData;


