<template>
  <div class="w1200">
    <p class="contentInfo"></p>
    <section class="contentMain clearfixs">
      <section class="pm-t puu-params">
        <el-row :gutter="20" type="flex">
          <el-col :span="3">
            <img :src="images.add" alt="">
            <span></span>
          </el-col>
          <el-col :span="3">
            <el-upload class="upload-demo" action="https://jsonplaceholder.typicode.com/posts/" :on-preview="handlePreview"
              :on-remove="handleRemove" :before-remove="beforeRemove" :limit="1" :on-exceed="handleExceed" :file-list="fileList"
              :show-file-list="false">
              <img :src="images.importgroup" alt="">
              <span></span>
            </el-upload>
          </el-col>
          <el-col :span="3">
            <img :src="images.exportgroup" alt="">
            <span></span>
          </el-col>
          <el-col :span="3">
            <img :src="images.del" alt="">
            <span></span>
          </el-col>
          <el-col :span="5" :offset="2">
            <el-input size="small" class="p-m-input" v-model="parame.words" placeholder="">
            </el-input>
          </el-col>
          <el-col :span="4">
            <el-button @click="search" class="search-btn" size="small"></el-button>
            <el-button @click="clear" class="return-btn" size="small"></el-button>
          </el-col>
        </el-row>
      </section>
      <section class="el-table-wrap">
        <el-table :data="tableData" @selection-change="handleSelectionChange" style="width: 100%">
          <el-table-column type="selection" width="55">
          </el-table-column>
          <el-table-column prop="prisonerName" label="">
          </el-table-column>
          <el-table-column prop="pCoding" label="">
          </el-table-column>
          <el-table-column prop="age" label="">
          </el-table-column>
          <el-table-column prop="pType.label" label="">
          </el-table-column>
          <el-table-column prop="comeTime" label="">
          </el-table-column>
          <el-table-column prop="crime.label" label="">
          </el-table-column>
          <el-table-column prop="during" label="">
          </el-table-column>
          <el-table-column label="" width="255">
            <template slot-scope="scope">
              <div class="operating">
                <router-link tag="span" to="/systemset/prisonermanagement/operation">
                  <img :src="images.review" alt=""> 
                </router-link>
                <router-link tag="span" to="/systemset/prisonermanagement/operation">
                  <img :src="images.edit" alt=""></router-link>
                <span @click="handleDelete(scope.$index, scope.row)">
                  <img :src="images.del" alt=""></span>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </section>
        <div class="el-pagination-wrap">
          <table-pagination :total="count" @change="pageChange"></table-pagination>
        </div>
    </section>
    <el-dialog title="" :visible.sync="dialogVisible" width="30%">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogVisible = true"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import tablePagination from '@/components/commons/tablePage.vue';

  import add from '@/assets/add.png';
  import del from '@/assets/del.png';
  import edit from '@/assets/edit.png';
  import review from '@/assets/review.png';
  import exportgroup from '@/assets/exportgroup.png';
  import importgroup from '@/assets/importgroup.png';

  export default {
    name: 'violation',
    components: {
      tablePagination
    },
    data() {
      return {
        count: 0, //
        images: {
          add: add,
          del: del,
          edit: edit,
          review: review,
          exportgroup: exportgroup,
          importgroup: importgroup,
        },
        parame: {
          words: ''
        },
        pagination: {
          totalRows: 0
        },
        multipleSelection: [],
        fileList: [{
          name: 'food.jpeg',
          url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
        }, {
          name: 'food2.jpeg',
          url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
        }],
        dialogVisible: false,
        value7: '',
        value: '',
        // 
        input: '',
        tableData: []
      }
    },
    mounted() {
      this.getTableDatas();
    },
    methods: {
      pageChange(page) {
        this.currentPage = page
        this.getTableDatas(page)
      },
      // 
      getTableDatas(page) {
        let _this = this;
        this.parame = {
          "page": page || 1,
          words: this.words
        }
        this.$ajxj.post('/getPManageDatas', this.parame).then(function (res) {
          console.log(res.data.totalRows);
          _this.count = res.data.totalRows;
          _this.tableData = res.data.items;
        }).catch(function (error) {
          console.log(error);
        }).then(function (error) {
          console.log(error);
        });
      },
      changeCurrent(val) {
        this.getTableDatas(val);
      },
      // 
      handleSelectionChange(val) {
        this.multipleSelection = val;
        console.log(this.multipleSelection)
      },
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      // 
      handleReview(index, row) {
        console.log(index, row);
      },
      // 
      handleDelete(index, row) {
        this.tableData.splice(index, 1);
        console.log(this.tableData);
      },
      // 
      delGroup() {
        if (this.multipleSelection.length === 0) {
          this.$message("!")
        } else {
          // 
          this.$confirm('')
            .then(_ => {
              var names = this.multipleSelection.map(item => item.name).join()
              console.log(names);
            })
            .catch(_ => {});
        }
      },
      // 
      search() {
        this.getTableDatas();
      },

      clear() {
        this.parame = {
          words: ''
        };
        this.getTableDatas();
      },
      handleExceed(files, fileList) {
        this.$message.warning(` 1  ${files.length}  ${files.length + fileList.length} `);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(` ${ file.name }`);
      }
    }
  }

</script>
<style scoped>
  .pm-t span {
    color: #666;
  }

</style>



