<template>
  <div id="posunusual">
    <p class="contentInfo"></p>
    <section class="contentMain">
      <section class="puu-params">
        <el-row>
          <el-col :span="3">
            <span>:</span>
            <el-select size="small" v-model="params.warningType" placeholder="">
              <el-option v-for="item in warningTypes" :key="item.value" :label="item.label" :value="item.value"></el-option>
            </el-select>
          </el-col>
          <el-col :span="3">
            <span>:</span>
            <el-select size="small" v-model="params.warningType" placeholder="">
              <el-option v-for="item in warningTypes" :key="item.value" :label="item.label" :value="item.value"></el-option>
            </el-select>
          </el-col>
          <el-col :span="5">
            <span>:</span>
            <el-select size="small" v-model="params.warningType" placeholder="">
              <el-option v-for="item in warningTypes" :key="item.value" :label="item.label" :value="item.value"></el-option>
            </el-select>
          </el-col>
          <el-col :span="5">
            <span>:</span>
            <el-select size="small" v-model="params.warningType" placeholder="">
              <el-option v-for="item in warningTypes" :key="item.value" :label="item.label" :value="item.value"></el-option>
            </el-select>
          </el-col>
          <el-col :span="7">
            <span>:</span>
            <el-input size="small" class="pp-input" v-model="params.prisonerName" placeholder="" clearable>
              <el-button slot="append" @click="filter"></el-button>
            </el-input>
          </el-col>
          <el-col :span="1">
            <el-button class="primary" size="small" @click="clear()"></el-button>
          </el-col>
        </el-row>


      </section>
      <section class="el-table-wrap">
        <el-table :data="prisonerInfo" stripe style="width: 100%;">
          <el-table-column prop="startTime" label=""></el-table-column>
          <el-table-column prop="endTime" label=""></el-table-column>
          <el-table-column prop="timeLen" label=""></el-table-column>
          <el-table-column prop="prisonerType" label="/"></el-table-column>
          <el-table-column prop="warningType" label=""></el-table-column>
          <el-table-column prop="prisonerName" label=""></el-table-column>
          <el-table-column prop="warningArea" label=""></el-table-column>
          <el-table-column prop="warningArea" label=""></el-table-column>
          <el-table-column prop="warningArea" label=""></el-table-column>
          <el-table-column prop="warningArea" label=""></el-table-column>
          <el-table-column prop="warningArea" label=""></el-table-column>
          <el-table-column prop="warningArea" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button @click="showVideo(scope.$index, scope.row)" size="mini"></el-button>
            </template>
          </el-table-column>
          <el-table-column prop="warningArea" label=""></el-table-column>
        </el-table>
        <div class="el-pagination-wrap text-center">
          <!-- <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="pagination.currentPage"
            :page-size="10" layout="prev, pager, next, jumper" :total="pagination.totalRows">
          </el-pagination> -->
          <table-pagination :total="count" @change="pageChange"></table-pagination>
        </div>
      </section>
    </section>

  </div>
</template>

<script>
  import tablePagination from '@/components/commons/tablePage.vue';
  export default {
    name: 'posunusual',
    components: {
      tablePagination
    },
    data() {
      return {
        count: 0, //
        message: "",
        params: {
          warningType: "0",
          prisonerType: "0",
          prisonerName: "",
          period: [new Date(), new Date()]
        },
        prisonerInfo: [],
        warningTypes:[],
        prisonerTypes: [],
        ppuTableDatas: [],
      }
    },
    methods: {
      pageChange(page) {
        this.currentPage = page
        this.getCameraList(page)
      },
      filter() {

      },
      clear: function () {
        this.params.period = [new Date(), new Date()];
        this.params.warningType = "";
        this.params.prisonerType = "";
        this.params.prisonerName = "";
      },

      showVideo: function (index, row) {
        this.$router.push({
          path: "/personnelposition"
        });
      },
      getCameraList: function (page) {
        var _this = this;
        this.$ajxj.post('/getCameraList',{page:page}).then(function (res) {
          console.log('res',res);
          
          _this.prisonerInfo = res.data.items;
          _this.count = res.data.totalRows
        }).catch(function (error) {}).then(function (error) {
          console.log(error);
        });
      }
    },
    mounted() {
      this.getCameraList();
    }
  }

</script>

<style scoped>
  .el-select {
    width: 58%;
  }

  /* .puu-params {
    padding: 0 1%;
    height: 60px;
    line-height: 60px;
    vertical-align: middle;
    font-size: 14px;
  }

  .puu-params .el-select,
  .puu-params .pp-input {
    width: 8%;
    margin: 0px 20px 0px 5px;
  } */

  .puu-item-popover {
    font-size: 12px;
  }

  .pp-input {
    width: 78%;
  }

</style>



