<template>
  <div>
    <section class="left-tree">
      <!-- <v-tree :tree-data="Prisonareatree" v-on:handle-node-click="handleNodeClick"></v-tree> -->
      <el-tree :data="treeData" node-key="id" @node-click="handleNodeClick" default-expand-all :expand-on-click-node="false">
        <span class="custom-tree-node" slot-scope="{ node, data }">
          <span v-if="data.isWarning == false"><i :class="node.icon"></i>{{ node.label }}</span>
          <span v-if="data.isWarning == true"><i><img :src="images.warning"></i>{{ node.label }}</span>
        </span>
      </el-tree>
    </section>
    <section class="right-main">
      <!--  -->
      <section v-show="isShowPrison" class="item prison area">
        <h3 class="title text-center"></h3>
        <img :src="images.area" alt="">
      </section>
      <!--  -->
      <section v-show="isShowfloor" class="item prison"></section>
      <!--  -->
      <section v-show="isShowMonitorArea" class="item prison"></section>
      <!--  -->
      <section v-show="isShowMonitorHouse" class="monitor-house clearfix">
        <!--  -->
        <section class="center fl">
          <h3 class="title text-center"></h3>
          <img :src="images.floor" alt="">
        </section>
        <!-- <section class="center fl">

        </section> -->
        <section class="right fr">
          <section class="house-floor">
            <p class="item-header"></p>
            <div class="block">
              <el-carousel trigger="click" height="150px" :autoplay="false" indicator-position="none" arrow="always">
                <el-carousel-item v-for="item in floors" :key="item">
                  <img :src="item" alt="">
                </el-carousel-item>
              </el-carousel>
            </div>
          </section>
          <section class="house-p">
            <p class="item-header"></p>
            <div>
              <table>
                <thead>
                  <tr>
                    <th>
                      <div class="cell"></div>
                    </th>
                    <th>
                      <div class="cell"></div>
                    </th>
                    <th>
                      <div class="cell"></div>
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item,index) in pPositionData" :key="item.name">
                    <td>
                      <div class="cell">{{index+1}}</div>
                    </td>
                    <td>
                      <div class="cell">{{item.name}}</div>
                    </td>
                    <td>
                      <div class="cell">{{item.numbering}}</div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </section>
        </section>
      </section>
    </section>
  </div>
</template>
<style scoped>
  .left-tree {
    width: 15%;
    display: inline-block;
    position: fixed;
    top: 60px;
    left: 0;
    height: 100%;
    background: #fff;
  }

  .right-main {
    background: #f3f6f8;
    margin-left: 15%;
    width: 83%;
    padding: 20px 1%;
  }

  .right-main .item {
    border: 1px solid #e0e3ec;
  }

  .title {
    height: 50px;
    line-height: 50px;
    font-size: 26px;
  }
  .area {
    height: 1100px;
    background: #fff;
    border: 1px solid #e0e3ec;
  }

  .right-main .monitor-house .center {
    display: inline-block;
    width: 73%;
    height: 1100px;
    background: #fff;
    border: 1px solid #e0e3ec;
  }

  .right-main .monitor-house .right {
    display: inline-block;
    width: 25%;
    /* border: 1px solid #e0e3ec; */
  }

  .right-main .monitor-house .right .house-floor,
  .right-main .monitor-house .right .house-p {
    border: 1px solid #e0e3ec;
  }

  .right-main .monitor-house .right .house-floor {
    margin-bottom: 20px;
  }

</style>
<script>
  import right from '@/assets/right.gif';
  import left from '@/assets/left.gif';
  import video from '@/assets/video.png';
  import warning from '@/assets/warning.png';

  import area from '@/assets/area.png';

  import floor from '@/assets/floor.png';
  import floor1 from '@/assets/floor1.png';
  // import floor1 from '@/assets/floor.png';

  // import vTree from '@/components/commons/tree.vue';
  export default {
    name: 'tvmonitor',
    // components: {
    //   vTree
    // },
    data() {
      return {
        images: {
          right: right,
          left: left,
          video: video,
          warning: warning,
          area: area,
          floor: floor
        },
        floors: [floor, floor1],
        // 
        isShowPrison: true,
        isShowfloor: false,
        isShowMonitorArea: false,
        isShowMonitorHouse: false,

        Prisonareatree: [],
        pPositionData: [],
        treeData: []
      }
    },
    created: function () {
      var _this = this;
      this.$ajxj.get('/getPrisonareatree')
        .then(function (res) {
          _this.treeData = res.data;
        })
        .catch(function (error) {
          console.log(error);
        })
        .then(function () {});

      this.$ajxj.get('/pPositionData')
        .then(function (res) {
          // _this.loading = false;
          _this.pPositionData = res.data.data
          _this.page = res.data.total
        }).catch(function (error) {
          console.log(error);
        }).then(function () {});
    },
    methods: {
      // handleNodeClick: function (data) {
      //   this.pnMain = true;
      // },
      handleNodeClick(data, d1, d2) {
        this.isShowMonitorHouse = true;
        this.isShowPrison = false;
      }
    }
  }

</script>



