import Mock from 'mockjs';

let calendarData = Mock.mock({
    dateTypes: [
        {value: "1", label: ""},
        {value: "2", label: ""},
        {value: "3", label: ""}
    ],
    areas: [
        {value: "1", label: ""},
        {value: "2", label: ""},
        {value: "3", label: ""},
        {value: "4", label: ""}
    ],
    options: [
        {value: "1", label: ""},
        {value: "2", label: ""}
    ],
    dailyDates :  {
        "totalRows": Mock.mock('@natural(1, 10)'),
        items: []
    },
    specialDates :  {
        "totalRows": Mock.mock('@natural(1, 10)'),
        items: []
    },
    holidayDates: [{
        name: '',
        startDate: '2018-01-01',
        endDate: '2018-01-01',
        dateType: ''
    }, {
        name: '',
        startDate: '2018-02-15',
        endDate: '2018-02-21',
        dateType: ''
    }, {
        name: '',
        startDate: '2018-05-01',
        endDate: '2018-05-01',
        dateType: ''
    }, {
        name: '',
        startDate: '2018-10-01',
        endDate: '2018-10-07',
        dateType: ''
    }]
})

for (let i = 0; i < 5; i++) {
    calendarData.dailyDates.items.push({
        startTime: "12:22:12",
        endTime: "12:22:12",
        dateType: "",
        timeLen: "8.3",
        item: "",
        area: "",
        option: "",
        scene: ""
    });
}

for (let i = 0; i < 5; i++) {
    calendarData.specialDates.items.push({
        startTime: "12:22:12",
        endTime: "12:22:12",
        dateType: "//",
        timeLen: "3720",
        item: "",
        area: "",
        option: "",
        scene: ""
    });
}

export default calendarData;


