<template>
    <div id="holidyDialog">
        <div id="tip">
          <span></span>
        </div>
        <!--  -->
        <vue-event-calendar
            :events="demoEvents"
            :feedbackDay="feedbackDay"
            @day-changed="handleDayChanged"
            @month-changed="handleMonthChanged">
        </vue-event-calendar>
        <!--  -->
        <div class="legend">
          <div class="title"></div>
          <div id="d1">
            <span id="s1"></span>
            <span></span>
          </div>
          <div id="d2">
            <span id="s2"></span>
            <span></span>
          </div>
          <div id="d3">
            <span id="s3"></span>
            <span></span>
          </div>
        </div>
        <!--  -->
        <div class="action">
          <span @click="addRow"></span>
        </div>
        <!--  -->
        <div class="events-table">
          <div class="grid-head">
            <table>
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
            </table>
          </div>
          <div class="grid-body">
            <table>
              <tbody>
                <tr v-for="(event, index) in showEvents">
                  <td @click="cellClick(index, 'title')">
                    <el-input v-model="event.title" autofocus="true" placeholder="" v-if="seen==('title'+index)" @blur="seen=null" ></el-input>
                    <span style="margin-left: 10px" v-else>{{ event.title }}</span>
                  </td>
                  <td @click="cellClick(index, 'beginDate')">
                    <el-date-picker v-model="event.beginDate" type="date" placeholder="" v-if="seen==('beginDate'+index)" @blur="seen=null" format="yyyy/MM/dd" value-format="yyyy/MM/dd"></el-date-picker>
                    <span style="margin-left: 10px" v-else>{{ event.beginDate }}</span>
                  </td>
                  <td @click="cellClick(index, 'endDate')">
                    <el-date-picker v-model="event.endDate" type="date" placeholder="" v-if="seen==('endDate'+index)" @blur="seen=null" format="yyyy/MM/dd" value-format="yyyy/MM/dd"></el-date-picker>
                    <span style="margin-left: 10px" v-else>{{ event.endDate }}</span>
                  </td>
                  <td>
                    <el-select v-model="event.type" placeholder="">
                      <el-option :label="''" :value="2"></el-option>
                      <el-option :label="''" :value="1"></el-option>
                    </el-select>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
      </div>
      <!--  -->
      <div id="buttons">
        <button class="ok"></button>
        <button class="can"></button>
      </div>
    </div>
</template>

<script>
    import Vue from 'Vue';
    import holiday from './2018Holiday.js';
    import { isBeginEndDate, dateTimeFormatter } from "../../../commons/vue-event-calendar/tools.js";
    export default {
        data() {
            return {
                seen: null,
                message: "",
                tableData: [],
                demoEvents: holiday,
                feedbackDay: {},
                clickDate: null,
                clickDateEvent: {},
            }
        },
        computed: {
          showEvents () {
            if (!this.clickDate || !this.clickDayEvent) {
              return this.demoEvents;
            }
            return isBeginEndDate(this.clickDate, this.clickDayEvent.beginDate, this.clickDayEvent.endDate) ? [this.clickDayEvent]:this.demoEvents;
          }
        },
        methods: {
            cellClick(index, key) {
              this.seen = key + index;
            },
            getTypeText(type) {
              let map = {
                '1': '',
                '2': ''
              }
              return map[type];
            },
            handleItemClick(event) {
              this.feedbackDay = event;
            },
            handleDayChanged(data) {
              this.clickDate = data.date;
              this.clickDayEvent = data.event;
            },
            handleMonthChanged(data) {

            },
            initTableData: function() {
                var _this = this;
                this.$ajxj.get('/getHolidayDates').then(function (respnose) {
                    _this.tableData = respnose.data;
                }).catch(function (error) {
                }).then(function () {
                    //console.log(error);
                });
            },
            addRow: function() {
                var newRow = {
                    title: '',
                    beginDate: dateTimeFormatter(new Date(), 'yyyy/MM/dd'),
                    endDate: dateTimeFormatter(new Date(), 'yyyy/MM/dd'),
                    type: 2
                }
                this.demoEvents.unshift(newRow);
            },
            deleteRow: function(index, row) {
                this.tableData.splice(index, 1);
            },
            save: function() {
                alert(":" + this.tableData.length + "");
            }
        },
        mounted() {
            this.initTableData();
        },
        props: ['dateTypes']
    }
</script>

<style>

    #holidyDialog {
      width: 600px;
      height: 785px;
    }

    .el-dialog__body {
        padding: 0px 20px;
    }

    #tip {
      line-height: 14px;
      margin-bottom: 18px;
    }

    #tip > span{
      width:196px;
      height:14px;
      font-size:14px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:#666666;
      line-height:25px;
    }

    .action {
      line-height: 14px;
      margin: 0 0 20px;
    }

    .el-dialog__header {
      padding: 0;
      height: 54px;
      line-height: 54px;
    }

    .el-dialog__header > .el-dialog__title {
        padding: 20px;
        width:70px;
        height:14px;
        font-size:14px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(89,196,238,1);
        line-height:25px;
    }

    .legend {
      margin-left: 24px;
      display: inline-block;
    }

    .legend > .title {
      color: #59C4EE;
      margin-bottom: 16px;
    }

    .legend > div {
      height: 16px;
    }

    .legend > div > span{
      width:40px;
      height:14px;
      font-size:14px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:#666666;
      line-height:25px;
    }

    .legend #s1 {
      height: 15px;
      width: 15px;
      background: #FFF1EF;
      display: inline-block;
    }

    .legend #s2 {
      height: 15px;
      width: 15px;
      background: #EEF9FD;
      display: inline-block;
    }

    .legend #s3 {
      height: 15px;
      width: 15px;
      background: #E0E3EC;
      display: inline-block;
    }

    .legend #d1 {
      margin-bottom: 13px;
    }

    .legend #d2 {
      margin-bottom: 12px;
    }

    .events-table {
      width: 569px;
    }

    .events-table table {
      width:569px;
      height:246px;
      border: 1px solid #E0E3EC;
    }

    .events-table > .grid-head > table > thead > tr > th{
      width:42px;
      height:13px;
      font-size:12px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:#333333;
      line-height:25px;
      text-align: center;
    }

    .events-table > .grid-body {
      overflow-x: hidden;
      width: 569px;
      height: 200px;
    }

    .events-table > .grid-body > table > tbody > tr{
      width:40px;
      line-height: 42px;
    }

    .events-table > .grid-body > table > tbody > tr > td{
      width:70px;
      height:12px;
      font-size:14px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:#666666;
      line-height:25px;
      text-align: center;
    }

    .events-table > .grid-body > table > tbody > tr > td > .el-date-editor.el-input {
      width: 132px;
    }

    .events-table > .grid-body > table > tbody > tr > td > .el-select{
      width: 104px;
    }

    #buttons {
      margin-top: 30px;
      padding: 0;
      line-height: 30px;
      text-align: right;
      width: 570px;
    }

    #buttons > button {
      width: 80px;
      height: 30px;
      border-radius:2px;
      font-size:14px;
      font-family:PingFang-SC-Regular;
      font-weight:400;
      line-height:15px;
    }

    #buttons > .ok {
      background: #59C4EE;
      color:#FFFFFF;
    }

    #buttons > .can {
      background: #FFFFFF;
      color:#59C4EE;
      border:1px solid #59C4EE;
    }

</style>



