<template>
  <div class="w1200">
    <section>
      <section class="pp-r-t q-v-t filter-wrap">
        <section>
          <el-row :gutter="20">
            <el-col :span="9">
              <span class="block">
                <span class="demonstration"></span>
                <el-date-picker size="mini" v-model="parame.startTime" type="datetime" placeholder="">
                </el-date-picker>
                <span>-</span>
                <el-date-picker size="mini" v-model="parame.endTime" type="datetime" placeholder="">
                </el-date-picker>
              </span>
            </el-col>
            <el-col :span="5">
              <span></span>
              <el-select size="small" v-model="parame.warningType" placeholder="">
                <el-option v-for="item in warningType" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-col>
            <el-col :span="7">
              <span>/</span>
              <el-input size="small" class="pp-input" v-model="parame.prisonerName" placeholder=""></el-input>
            </el-col>
            <el-col :span="3">
              <el-button @click="search" class="search-btn" size="small"></el-button>
              <el-button @click="clearFilter" class="return-btn" size="small"></el-button>
            </el-col>
          </el-row>
        </section>
      </section>
      <section class="el-table-wrap clearfix">
        <el-table :data="ppuTableDatas" style="width: 100%">
          <el-table-column prop="startTime" label="">
          </el-table-column>
          <el-table-column prop="endTime" label="">
          </el-table-column>
          <el-table-column prop="timeLen" label="">
          </el-table-column>
          <el-table-column prop="prisonerName" label="">
          </el-table-column>
          <el-table-column prop="warningType" label="">
          </el-table-column>
          <el-table-column prop="warningArea" label="">
          </el-table-column>
          <el-table-column prop="warningRule" label="">
          </el-table-column>
          <el-table-column prop="warningTv" label="">
            <template slot-scope="scope">
              <div class="operating">
                <router-link tag="span" to="/systemset/prisonermanagement/operation">
                  <img :src="images.review" alt=""> 
                </router-link>
              </div>
            </template>
          </el-table-column>
        </el-table>
        <div class="el-pagination-wrap fr">
          <!-- <el-pagination background layout="prev, pager, next, jumper" @current-change="changeCurrent()" :page-size="pagination.pageSize"
            :total="pagination.totalRows" :current-page.sync="pagination.currentPage">
          </el-pagination> -->
          <table-pagination :page-index="currentPage" :total="count" :page-size="pageSize" @change="pageChange"></table-pagination>
        </div>
      </section>
    </section>
  </div>
</template>

<script>
  import review from '@/assets/review.png';
  import tablePagination from '@/components/commons/tablePage.vue';
  export default {
    name: 'violation',
    components: {
      tablePagination
    },
    data() {
      return {
        pageSize: 10, //20
        currentPage: 1, //
        count: 0, //
        
        ppuTableDatas: [],
        images: {
          review: review
        },
        parame: {
          startTime: '',
          endTime: '',
          warningType: '',
          prisonerName: '',
        },
        warningType: [],
        // 
        // pickerOptions2: {
        //   shortcuts: [{
        //     text: '',
        //     onClick(picker) {
        //       const end = new Date();
        //       const start = new Date();
        //       start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
        //       picker.$emit('pick', [start, end]);
        //     }
        //   }, {
        //     text: '',
        //     onClick(picker) {
        //       const end = new Date();
        //       const start = new Date();
        //       start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
        //       picker.$emit('pick', [start, end]);
        //     }
        //   }, {
        //     text: '',
        //     onClick(picker) {
        //       const end = new Date();
        //       const start = new Date();
        //       start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
        //       picker.$emit('pick', [start, end]);
        //     }
        //   }]
        // },
        pagination: {
          pageSize: 10,
          currentPage: 1,
          totalRows: 100
        },
        value: '',
        // 
        input: '',
      }
    },
    mounted() {
      this.getWarningType();
      this.getTableDatas();
    },
    methods: {
      pageChange(page) {
        this.currentPage = page
        this.getTableDatas(page)
      },
      // 
      getTableDatas(page) {
        let _this = this;
        this.parame = {
          "page": page || 1,
          "startTime": this.parame.startTime,
          "endTime": this.parame.endTime,
          "warningType": this.parame.warningType,
          "prisonerName": this.parame.prisonerName,
        }
        this.$ajxj.post('/getPosunusualItems', this.parame).then(function (res) {
          console.log('res', res);
          _this.count = res.data.totalRows;
          _this.ppuTableDatas = res.data.items;
          _this.pagination.totalRows = res.data.totalRows;
        }).catch(function (error) {
          console.log(error);
        }).then(function (error) {
          console.log(error);
        });
      },
      // 
      getWarningType() {
        let _this = this;
        this.$ajxj.post('/getPosunusualItems').then(function (res) {
          res.data = [{
            value: '1',
            label: ''
          }, {
            value: '2',
            label: ''
          }, {
            value: '3',
            label: ''
          }, {
            value: '4',
            label: ''
          }, {
            value: '5',
            label: ''
          }];
          _this.warningType = res.data;
        }).catch(function (error) {
          console.log(error);
        }).then(function (error) {
          console.log(error);
        });
      },

      // 
      search() {
        this.getTableDatas();
      },
      clearFilter() {
        this.parame = {
          startTime: '',
          endTime: '',
          warningType: '',
          prisonerName: ''
        };
        this.getTableDatas();
      },
      changeCurrent: function () {
        this.getTableDatas(this.pagination.currentPage);
      },
    }
  }

</script>
<style scoped>
  /* .q-v-t {
    height: 70px;
    line-height: 70px;
  } */

  .q-v-t .el-date-editor.el-input,
  .q-v-t .el-date-editor.el-input__inner {
    width: 40%;
  }

  .q-v-t section {
    display: inline-block;
    margin-right: 1%;
  }

  .q-v-t .el-select,
  .q-v-t .pp-input {
    width: 49%;
  }

</style>



