<template>
  <div id="posunusual">
    <section class="puu-params">
      <el-row style="margin: 0px 10px;">
        <el-col :span="6">
          <span>
            <span class="puu-params-label">:</span>
            <el-date-picker style="width: 40%" size="small" v-model="params.startTime" type="datetime" placeholder=""></el-date-picker>
            <span>-</span>
            <el-date-picker style="width: 40%" size="small" v-model="params.endTime" type="datetime" placeholder=""></el-date-picker>
          </span>
        </el-col>
        <el-col :span="5">
          <span class="puu-params-label">:</span>
          <el-select size="small" v-model="params.warningType" placeholder="">
            <el-option v-for="item in warningTypes" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-col>
        <el-col :span="5">
          <span class="puu-params-label">:</span>
          <el-select size="small" v-model="params.prisonerType" placeholder="">
            <el-option v-for="item in prisonerTypes" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-col>
        <el-col :span="6">
          <span class="puu-params-label">/:</span>
          <el-input size="small" class="puu-input" v-model="params.prisonerName" placeholder="" clearable></el-input>
        </el-col>
        <el-col :span="1">
          <el-button size="mini" type="primary" class="search-btn" @click="doQuery()"></el-button>
        </el-col>
        <el-col :span="1">
          <el-button size="mini" type="primary" class="search-btn" @click="clear()"></el-button>
        </el-col>
      </el-row>
    </section>
    <section class="puu-items">
      <el-table :data="ppuTableDatas" stripe style="width: 100%;" height="780">
        <el-table-column prop="startTime"    label="" min-width="170px"></el-table-column>
        <el-table-column prop="endTime"      label="" min-width="170px"></el-table-column>
        <el-table-column prop="timeLen"      label=""     min-width="120px"  align="center"></el-table-column>
        <el-table-column prop="prisonerName" label="" min-width="120px"  align="center">
          <template slot-scope="scope">
            <el-popover placement="bottom-start" width="240" trigger="hover" @show="showPrisoner()">
              <el-row style="margin-bottom: 0px;">
                <el-col :span="11">
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.prisonerName }}</div>
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.prisonerNum }}</div>
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.sex }}</div>
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.age }} </div>
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.room }}</div>
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.group }}</div>
                </el-col>
                <el-col :span="13"><img src="@/assets/pp-p.png" style="width:140px; height:90px;"></el-col>
              </el-row>
              <el-row>
                <el-col :span="11">
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.prisonerType }}</div>
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.count }} </div>
                </el-col>
                <el-col :span="13">
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.crime }}</div>
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.crimeTime }}</div>
                </el-col>
              </el-row>
              <div style="color: #59c4ee" slot="reference">{{ scope.row.prisonerName }}</div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column prop="prisonerNum"  label=""  min-width="120px" align="center">
          <template slot-scope="scope">
            <el-popover placement="bottom-start" width="240" trigger="hover" @show="showPrisoner()">
              <el-row style="margin-bottom: 0px;">
                <el-col :span="11">
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.prisonerName }}</div>
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.prisonerNum }}</div>
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.sex }}</div>
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.age }} </div>
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.room }}</div>
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.group }}</div>
                </el-col>
                <el-col :span="13"><img src="@/assets/pp-p.png" style="width:140px; height:90px;"></el-col>
              </el-row>
              <el-row>
                <el-col :span="11">
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.prisonerType }}</div>
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.count }} </div>
                </el-col>
                <el-col :span="13">
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.crime }}</div>
                  <div class="puu-item-popover"><span></span>{{ prisonerInfo.crimeTime }}</div>
                </el-col>
              </el-row>
              <div style="color: #59c4ee" slot="reference">{{ scope.row.prisonerNum }}</div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column prop="prisonerType" label=""  min-width="120px" align="center"></el-table-column>
        <el-table-column prop="warningType"  label=""  min-width="120px" align="center"></el-table-column>
        <el-table-column prop="warningArea"  label=""  min-width="170px"></el-table-column>
        <el-table-column label="" fixed="right" width="120px" align="center">
          <template slot-scope="scope">
            <el-button style="padding: 0px 15px;" @click="showVideo(scope.$index, scope.row)" size="mini" type="text">
              <img :src="images.video" style="display: inline-block; line-height: 20px; vertical-align: middle;">
              <span style="display: inline-block; line-height: 20px; vertical-align: middle;"></span>
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination layout="prev, pager, next, jumper" @current-change="changeCurrent()" :page-size="pagination.pageSize"
                     :total="pagination.totalRows" :current-page.sync="pagination.currentPage">
      </el-pagination>
    </section>
  </div>
</template>

<script>
  import video from '@/assets/video.png';

  export default {
    name: 'posunusual',
    data() {
      return {
        params: {
          startTime: '',
          endTime: '',
          warningType: "",
          prisonerType: "",
          prisonerName: ""
        },
        images: {
          video: video
        },
        pagination: {
          pageSize: 10,
          currentPage: 1,
          totalRows: 100
        },
        prisonerInfo: {},
        warningTypes: [],
        prisonerTypes: [],
        ppuTableDatas: []
      }
    },
    methods: {
      doQuery: function () {
        
      },
      clear: function () {
        this.params = {
          startTime: '',
          endTime: '',
          warningType: "",
          prisonerType: "",
          prisonerName: ""
        }
      },
      changeCurrent: function () {
        alert("" + this.pagination.currentPage + "");
      },
      showVideo: function (index, row) {
        this.$router.push({
          path: "/personnelposition"
        });
      },
      showPrisoner: function (page) {
        var _this = this;
        this.$ajxj.get('/getPrisonerInfo',{page:page}).then(function (respnose) {
          _this.prisonerInfo = respnose.data;
        }).catch(function (error) {}).then(function (error) {
          console.log(error);
        });
      }
    },
    mounted() {
      var _this = this;
      // 
      this.$ajxj.post('/getPosunusualItems').then(function (respnose) {
        _this.ppuTableDatas = respnose.data.items;
        _this.pagination.totalRows = respnose.data.totalRows;
      }).catch(function (error) {}).then(function (error) {
        console.log(error);
      });
    }
  }
</script>

<style scoped>
  .el-select {
    width: 55%;
  }

  .puu-input {
    width: 45%;
  }

  .puu-item-popover {
    font-size: 12px;
  }

  .el-pagination {
    text-align: right;
  }

  .puu-params-label {
    font-size: 14px;
  }

  .puu-params {
    background-color: #f3f6f8;
    position: fixed;
    top: 60px;
    z-index: 666;
    width: 100%;
    height: 60px;
    line-height: 60px;
  }

  .puu-items {
    position: absolute;
    top: 120px;
  }
</style>


