import Mock from 'mockjs';

let status = ["", "", "", "", "", "", "", ""];
let pstatus = [];
status.forEach(function (item) {
  pstatus.push({
    status: item,
    pNum: Mock.mock('@natural(60, 300)'),
  });
});

export default pstatus;



