<script>
  import currentPos from '@/assets/currentPos.png';
  import tablePagination from '@/components/commons/tablePage.vue';
  export default {
    name: 'personnelposition',
    components: {
      tablePagination
    },
    data() {
      return {
        currentPos: currentPos,
        input: '',
        loading: false,
        pPositionData: [],

        pageSize: 10, //20
        currentPage: 1, //
        count: 0, //

        pPTableData: [],
        options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        }, {
          value: '5',
          label: ''
        }],
        value: '',
        currentPage3: 1,
        page: 1,
        activeName: 'second',
        value6: '',
        value7: ''
      }
    },
    created: function () {
      var _this = this;
      this.$ajxj.get('/pPositionData')
        .then(function (res) {
          // _this.loading = false;
          _this.pPositionData = res.data.data
          _this.page = res.data.total
        }).catch(function (error) {
          console.log(error);
        }).then(function () {});
      this.getTableData();
    },
    mounted: function () {
      // console.log(document.getElementsByClassName("pp-wrap")[0]);
      // document.getElementsByClassName("pp-wrap")[0].style.width = document.body.clientWidth;

    },
    methods: {
      //pagepage
      pageChange(page) {
        this.currentPage = page
        this.getTableData(page)
      },
      getTableData(p) {
        var _this = this
        this.$ajxj.post('/pPTableData', {
            page: p || 1
          })
          .then(function (res) {
            _this.count = res.data.total;
            console.log('res.data', res.data)

            _this.pPTableData = res.data.data
            _this.page = res.data.total
          }).catch(function (error) {}).then(function () {});
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        this.getTableData(val);
        console.log(`: ${val}`);
      },
      handleEdit(index, row) {
        console.log(index, row);
        window.open(row.tv)
      },
      handleClick(tab, event) {
        console.log(tab, event);
      }
    }
  }

</script>
<style scoped>
  .pposition-l {
