  }

  .pposition-r {
    width: 85%;
    float: right;
  }

  /* begin */
  .el-tabs__header {
    display: inline-block;
    position: relative;
    margin: 0;
  }

  .el-tabs__header:after {
    display: block;
    content: '';
    position: absolute;
    top: 51%;
    background: #e9ebf0;
    width: 250%;
    height: 1px;
    left: 105%;
  }

  /* end */
  .el-tabs__nav {
    cursor: pointer;
  }

