    line-height: 48px;
    background-color: #fff;
    padding: 0 1%;
  }

  .pp-r-d {
    background-color: #f3f6f8;
    padding: 20px 1%;
  }

  .pp-r-d-l {
    width: 40%;
    display: inline-block;
  }

  .pp-r-d-l-l .el-select,
