  }

  .pp-r-d-l-l,
  .pp-r-d-l-r {
    width: 50%;
    display: inline-block
  }

  .pp-r-d-l-r .imgWrap {
    width: 95%;
    border: 1px solid #dcdcdc;
    position: relative;
    text-align: center;
    background-color: #fff;
    padding-top: 15px;
  }

  .imgWrap>span {
    position: absolute;
    left: 50%;
    left: 5%;
    top: -1.4%;
    width: 6em;
    background-color: #fff;
    height: 1px;
  }

  .imgWrap>span.pp-photo {
    width: 3em;
  }

  .imgWrap::before {
    content: attr(title);
    position: absolute;
    left: 5%;
    top: -0.7em;
    padding: 0 10px;
    z-index: 99;
  }

  .imgWrap:nth-child(2) {
    margin-top: 20px;
  }

  .pp-r-d-l-r .imgWrap>div {
    width: 45%;
    display: inline-block;
  }

  .pp-r-d-l-r .imgWrap>div>span {
    display: inline-block;
  }

  p.h-line {
    margin-bottom: 17px;
  }

  .pos-res {
    position: relative;
  }

  /*  */
  .pp-r-d-r-d p.h-line::after {
    top: 1%;
    width: 68%;
  }

  .pp-r-d .pp-r-d-l .pp-r-d-l-l div span:nth-child(1) {
    display: inline-block;
    width: 34%;
    text-align: right;
  }

  .pp-r-d-r {
    width: 60%;
    float: right;
  }

</style>



